import React from 'react';
import './sidebar.scss';
import './App.css';
import {Alerts,Patients} from './components/doctor_components';

export class DoctorLayout extends React.Component {
  constructor(props) {
    super(props);

    this.state = {
      leftOpen: false
    }
    
  }

  toggleSidebar = (event) => {
    let key = `${event.currentTarget.parentNode.id}Open`;
    this.setState({ [key]: !this.state[key] });
  }
//Style={color:"#F0F8FF",borderBottom:"5px solid #F0F8FF",position:"relative",bottom:"10px"}
  decide_color(){
    let Style;
    if(this.state.leftOpen)
      Style={color:"#F0F8FF",borderBottom:"5px solid #F0F8FF",position:"absolute",display:"block"}
    else  
      Style={color:"#4682B4",borderBottom:"5px solid #4682B4",position:"absolute",display:"block"}
    console.log(Style)
    return Style
  }

  render() {
    let leftOpen = this.state.leftOpen ? 'open' : 'closed';
    //style={{borderBottom:"5px solid #F0F8FF"}}
    let Style=this.decide_color()
    return (
      <div id='layout'>
          <div id='left' className={leftOpen} >
              <div  className='icon'  onClick={this.toggleSidebar} style={Style} >
                  &equiv;
              </div>
              <div className={`sidebar ${leftOpen}`} >
              {/* style={{borderBottom:"5px solid #F0F8FF"}} >  style for classname='header */}
                  <div className='header' style={{borderBottom:"5px solid #F0F8FF"}}>
                    <h3 className='tit' >
                      Covid-19 Patient Monitor
                    </h3>
                  </div>
                  <div className='content'>
                      <h3>
                        Welcome Doctor_Name <br></br>
                        Doc ID: 
                      </h3>
                      <p>
                        <button id="logout_btn">LOG OUT</button>
                      </p>
                  </div>
              </div>
          </div>

          <div id='main'>
              <div className='header' style={{color:"#4682B4"}}>
                  <h1 className={`
                      tit
                      ${'left-' + leftOpen}
                  `}>
                      Doctor Portal
                  </h1>
              </div>
              <div className='content'>
                  {/* <div className="leftline"></div> */}
                  <div className="alert_box">
                    <Alerts />
                  </div>
                  <div className="patient_box">
                    <Patients />
                  </div>
              </div>
          </div>


      </div>
    );
  }
}
