
import List from '../docpat.json'

export function Extract(name){
    console.log("inisde list ectractor method")
    let name_list=[],id_list=[],i=0
    List.forEach((p)=>{
        if(p.name===name){
            name_list=p.patients.map((e)=>e[0])
            id_list=p.patients.map((e)=>e[1])
        }
    })
    console.log(name_list,id_list)
    return [name_list,id_list]
}

export function PatTableExtract(){
    let name_list=[],id_list=[],i=0
    List.forEach((p)=>{
        name_list=p.patients.map((e)=>e[0])
        id_list=p.patients.map((e)=>e[1])
    })
    console.log(name_list)
    return [name_list,id_list]
}

export function doctableExtract(){
    let name_list=[],id_list=[],i=0
    List.forEach((p)=>{
        name_list.push(p.name)
        console.log("added ",name_list)
        id_list.push(p.d_id)
    })
    //console.log(name_list)
    return [name_list,id_list]
}
 
